export default function RootLayout({ children }) {
  return (
    <html lang="en" suppressHydrationWarning>
      <head>
        {/* Inline critical CSS for above-the-fold content - prevents render blocking */}
        <CriticalCSS />
        {/* Script to make all CSS load asynchronously - must run before CSS loads */}
        <Script
          id="async-css-loader"
          strategy="beforeInteractive"
          dangerouslySetInnerHTML={{
            __html: `
              (function() {
                // Make all existing stylesheets non-blocking immediately
                var links = document.querySelectorAll('link[rel="stylesheet"]:not([data-async])');
                links.forEach(function(link) {
                  link.setAttribute('data-async', 'true');
                  link.media = 'print';
                  link.onload = function() {
                    this.media = 'all';
                    this.onload = null;
                  };
                  // Fallback for browsers without onload support
                  setTimeout(function() {
                    if (link.media === 'print') {
                      link.media = 'all';
                    }
                  }, 50);
                });
                
                // Watch for new stylesheets added dynamically
                if (document.readyState === 'loading') {
                  document.addEventListener('DOMContentLoaded', function() {
                    var newLinks = document.querySelectorAll('link[rel="stylesheet"]:not([data-async])');
                    newLinks.forEach(function(link) {
                      link.setAttribute('data-async', 'true');
                      link.media = 'print';
                      link.onload = function() {
                        this.media = 'all';
                        this.onload = null;
                      };
                      setTimeout(function() {
                        if (link.media === 'print') {
                          link.media = 'all';
                        }
                      }, 50);
                    });
                  });
                }
              })();
            `,
          }}
        />
        {/* Resource hints for GTM - only preconnect, DNS prefetch can wait */}
        <link rel="preconnect" href="https://www.googletagmanager.com" crossOrigin="anonymous" />
        <link rel="dns-prefetch" href="https://www.googletagmanager.com" />
        <link rel="dns-prefetch" href="https://www.google-analytics.com" />
        {/* Preload critical resources */}
        <link rel="preload" href="/logo.webp" as="image" type="image/webp" fetchPriority="high" />
        <OrganizationSchema />
        <WebsiteSchema />
        <ScrollToTop />
        {/* Client-side CSS async loader for dynamically added stylesheets */}
        <AsyncCSSLoader />
      </head>
      {/* 
        Note: suppressHydrationWarning on body/html suppresses most hydration warnings.
        Some warnings from Next.js Link components (contenteditable/style attributes) 
        may still appear but are harmless - they're added by Next.js during hydration 
        for better UX and don't affect functionality.
      */}
      <body suppressHydrationWarning>
        <GTMConsentWrapper />
        <Layout>
          {children}
        </Layout>
      </body>
    </html>
  )
}
